import { createContext, useCallback, useState } from "react";

type RoundType = {
    round: number,
    name: string,
}
export type ClassType = {[key: string]: RoundType[]};
export type ContestType = {
    id: number,
    name: string,
    nameDisplay: string,
    startDate: Date,
    endDate: Date,
    place: string,
    host: string,
    rounds: ClassType,
};

export const defaultContestList:{
    contestList: ContestType[];
    setContestList: (value: ContestType[]) => void;
} = {
    contestList: [{
        id: 1,
        name: "400",
        nameDisplay: "400",
        startDate: new Date(),
        endDate: new Date(),
        place: "",
        host: "",
        rounds: {
            A: [{round: 1, name: "1"}, {round: 2, name: "2"}, {round: 3, name: "3"}],
            B: [{round: 1, name: "1"}, {round: 2, name: "2"}, {round: 3, name: "3"}, {round: 4, name: "4"}],
        },
    },{
        id: 2,
        name: "500",
        nameDisplay: "500",
        startDate: new Date(),
        endDate: new Date(),
        place: "",
        host: "",
        rounds: {
            A: [{round: 1, name: "1"}, {round: 2, name: "2"}],
            B: [{round: 1, name: "1"}, {round: 2, name: "2"}, {round: 3, name: "3"}],
            C: [{round: 1, name: "1"}, {round: 2, name: "2"}, {round: 3, name: "3"}, {round: 4, name: "4"}],
        },
    }],
    setContestList: (value: ContestType[]) => {},
};

export const ContestContext:
React.Context<{
    contestList: ContestType[];
    setContestList: (value: ContestType[]) => void;
}> = createContext(defaultContestList);

export const useContestContext: () => {
    contestList: ContestType[];
    setContestList: (value: ContestType[]) => void;
} = () => {
    const [contestList, _setContestList] = useState(defaultContestList.contestList);
    const setContestList = useCallback((value: ContestType[]) => {_setContestList(value)}, []);
    return {
        contestList,
        setContestList,
    }
};